a,
a:visited,
a:focus {
  text-decoration: none;
  color: #666;
}

a:hover {
  color: #000;
  text-decoration: none;
}

ul,
li,
ol {
  list-style: none;
  background: transparent;
  margin-bottom: 0;
}

.container {
  width: 1200px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}
.container::before, .container::after {
  display: table;
  content: " ";
}
.container::after {
  clear: both;
}

header {
  color: #333;
  background-color: #fff;
}
header .logo {
  display: inline-block;
  height: 60px;
  line-height: 60px;
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}
header .logo img {
  height: 60px;
}
header .nav-bar {
  float: right;
}
header .nav-bar .nav-toggle {
  display: none;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: inherit;
  text-rendering: auto;
  line-height: 80px;
  font-size: 2rem;
  font-style: normal;
}
header .nav-bar .nav-toggle::before {
  content: "\f0c9";
}
header .nav-bar li {
  list-style: none;
  display: inline-block;
  position: relative;
}
header .nav-bar li a {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  color: #666;
  font-size: 16px;
  margin: 20px 0;
}
header .nav-bar li.active {
  background-color: red;
}
header .nav-bar li.active > a {
  color: #fff;
}
header .nav-bar li .sub-menu {
  position: absolute;
  width: 100%;
  display: none;
  z-index: 9999;
  background: #FFF;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid #f00;
}
header .nav-bar li .sub-menu li {
  display: block;
  width: 100%;
  border-bottom: 1px solid #dcdcdc;
  position: relative;
  z-index: 9999;
}
header .nav-bar li .sub-menu li a {
  margin: 0;
}
header .nav-bar li:hover {
  background-color: rgba(255, 0, 0, 0.3);
}
header .nav-bar li:hover > a {
  color: #fff;
}
header .nav-bar li:hover .sub-menu {
  display: block;
}

footer {
  margin-top: 30px;
  padding: 30px 0;
  border-top: 1px solid #ddd;
  color: #666;
  background: #f1f1f1;
}
footer .copyright {
  text-align: center;
}

.banner-bg {
  background-repeat: no-repeat;
  background-size: cover;
  height: 140px;
}

.slider .slider-box img {
  height: 100%;
  width: 100%;
}
.slider .slider-point li {
  cursor: pointer;
}
.slider .slider-point li span {
  display: none;
}

.path {
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #f5f5f5;
  padding: 10px;
}
.path ul {
  display: inline-block;
}
.path ul li::after {
  padding-left: 10px;
}

#content {
  min-height: 30rem;
}
#content .title {
  margin-bottom: 10px;
  line-height: 30px;
  padding: 10px 0;
  font-size: 20px;
  border-bottom: 1px solid #dbdbdb;
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
}
#content .tags {
  line-height: 30px;
  margin: -5px 0 10px;
}
#content .tags span {
  margin: 0 5px;
  color: #9baab6;
}
#content .header {
  display: grid;
  grid-template-columns: 200px 1fr;
}
#content .header .thumb {
  text-align: center;
}
#content .header .thumb img {
  width: 120px;
}
#content .links {
  margin: 0 20px 30px;
}
#content .links a {
  display: inline-block;
  padding: 4px 10px;
  background-color: #f00;
  color: #fff;
}
#content .content img {
  max-width: 100%;
}

#content-list .item {
  display: grid;
  grid-template-columns: 163px 1fr;
  height: 130px;
  border-bottom: 1px dashed #EBEEF3;
  padding-top: 12px;
}
#content-list .item .thumb {
  text-align: center;
  width: 150px;
  height: 100px;
  overflow: hidden;
}
#content-list .item .thumb img {
  width: 100%;
  height: 100%;
}
#content-list .item .info .title {
  font-size: 18px;
  font-weight: bold;
}
#content-list .item .info .desc {
  font-size: 14px;
  color: #999;
  line-height: 1.6;
  margin-top: 6px;
  max-height: 40px;
  overflow: hidden;
}
#content-list .item .info .tags {
  margin-top: 9px;
  font-size: 14px;
  color: #999;
}
#content-list .item .info .tags span {
  margin: 0 5px;
}
#content-list .item:hover {
  background: #FAFBFC;
}

.theme-list .item {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 10px;
}
.theme-list .item .thumb {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  margin-top: 30px;
  padding: 10px;
  position: relative;
  height: 300px;
  overflow: hidden;
}
.theme-list .item .thumb img {
  border-radius: 2px;
  width: 100%;
  min-height: 100%;
  transition: all ease-in-out 2s;
  position: absolute;
  height: auto;
}
.theme-list .item .thumb .fa-search {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: all ease-in-out 0.3s;
  opacity: 0;
  font-size: 70px;
  padding-top: 113px;
  background-color: rgba(0, 0, 0, 0.3);
  color: rgba(255, 255, 255, 0.7);
}
.theme-list .item .title {
  margin: 16px auto 10px;
  width: 80%;
  white-space: nowrap;
  display: block;
  overflow: hidden;
  font-size: 18px;
  text-overflow: ellipsis;
  text-align: center;
}
.theme-list .item .title::after {
  content: " ";
  display: block;
  height: 3px;
  width: 40px;
  background-color: #00b19d;
  margin: 10px auto;
}
.theme-list .item .desc {
  color: #98a6ad;
  max-height: 45px;
  overflow: hidden;
  margin-bottom: 10px;
}
.theme-list .item:hover .thumb .fa-search {
  opacity: 1;
}

.tab-box .tab-header {
  border-bottom: 1px solid #ddd;
  height: 42px;
}
.tab-box .tab-header .tab-item {
  color: #555;
  cursor: default;
  height: 42px;
  line-height: 40px;
}
.tab-box .tab-header .tab-item.active {
  color: #000;
  background-color: #fff;
  border: 1px solid transparent;
  border-bottom-color: #f90;
}
.tab-box .tab-header .tab-item:hover {
  text-decoration: none;
  background-color: #eee;
}
.tab-box .tab-body {
  margin: 15px 0;
}

.thumb-box {
  display: grid;
  grid-template-columns: 117px 1fr;
  grid-gap: 20px;
}

.list-box {
  padding-left: 10px;
}
.list-box li {
  margin-bottom: 10px;
}
.list-box li a {
  height: 20px;
  line-height: 20px;
  display: block;
  overflow: hidden;
}
.list-box li a span {
  float: right;
}
.list-box li em {
  display: inline-block;
  vertical-align: 2px;
  width: 5px;
  height: 5px;
  background: #8BBEE4;
  border-radius: 50%;
  margin-right: 10px;
}

.title-bar {
  font-size: 18px;
  border-bottom: 1px solid #dcdcdc;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.right-box {
  margin-top: 20px;
}
.right-box .panel .header {
  font-size: 18px;
  border-bottom: 1px solid #dcdcdc;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.right-box .panel ul li {
  font-size: 14px;
  padding: 20px 0;
  border-bottom: 1px solid #f0f0f0;
  font-weight: 400;
}
.right-box .top-panel ul li {
  position: relative;
  padding: 5px 0;
}
.right-box .top-panel .item-top {
  background: #ED4138;
  border: 1px solid #db2b28;
  display: inline-block;
  width: 20px;
  height: 20px;
  color: #fff;
  text-align: center;
  margin-right: 20px;
}
.right-box .top-panel .item-score {
  float: right;
}

.tree-box li {
  line-height: 40px;
  padding: 0 10px;
}

.panel .panel-header {
  line-height: 40px;
  background-color: #fff;
  padding: 0 10px;
  border-bottom: 1px solid #f90;
}
.panel .panel-header .more {
  float: right;
}
.site-bg {
  min-height: 300px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: rgba(0, 0, 0, 0.1);
}

.search-box {
  position: relative;
  margin: 30px auto 40px;
  width: 700px;
}
.search-box .search-tips,
.search-box .search-engine {
  display: none;
}
.search-box .search-input {
  position: relative;
}
.search-box .search-input .search-icon {
  position: absolute;
  left: 3px;
  top: 5px;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 25px;
  background-size: 100%;
  cursor: pointer;
}
.search-box .search-input input {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  color: #999;
  border: none;
  outline: none;
  padding: 0 45px;
  border: 1px solid #e6e6e6;
}
.search-box .search-input .search-btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  line-height: 48px;
  background: transparent;
  border: none;
  font-size: 25px;
  color: #ddd;
  font-weight: bold;
  outline: none;
  padding: 0 10px;
}
.search-box .search-tips {
  position: absolute;
  z-index: 100;
  width: 100%;
  border-radius: 0 0 10px 10px;
  background: #FFF;
  box-shadow: 0px 4px 5px 0px #cccccc94;
  overflow: hidden;
}
.search-box .search-tips li {
  border-top: 1px solid #f2f2f2;
  line-height: 30px;
  font-size: 14px;
  padding: 0px 25px;
  color: #777;
  cursor: pointer;
}
.search-box .search-tips li span {
  display: inline-block;
  width: 20px;
  height: 20px;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  background: #e5e5e5;
  margin-right: 10px;
  border-radius: 10px;
  color: #999;
}
.search-box .search-tips li.active {
  background-color: rgba(153, 153, 153, 0.1);
}
.search-box .search-engine {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background: #FFF;
  padding: 15px 0 0 15px;
  border-radius: 5px;
  box-shadow: 0px 5px 20px 0px #d8d7d7;
  transition: all 0.3s;
  z-index: 999;
}
.search-box .search-engine .search-engine-header {
  padding-right: 10px;
}
.search-box .search-engine .search-engine-header .search-engine-tool {
  float: right;
}
.search-box .search-engine .search-engine-body li {
  display: inline-block;
  width: 140px;
  margin: 0 15px 15px 0;
  line-height: 30px;
  font-size: 14px;
  padding: 5px 10px 5px 10px;
  background: #f9f9f9;
  color: #999;
  cursor: pointer;
}
.search-box .search-engine .search-engine-body li span {
  width: 25px;
  height: 25px;
  border-radius: 15px;
  float: left;
  margin-right: 5px;
  margin-top: 2.5px;
  background-size: 100%;
}
.search-box .toggle-box {
  font-family: "Font Awesome 5 Free";
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  font-weight: 900;
  vertical-align: text-bottom;
  font-size: 20px;
}
.search-box .toggle-box::before {
  content: "\f204";
}
.search-box .toggle-box.checked::before {
  content: "\f205";
  color: #5cb85c;
}

.search-bar {
  padding: 10px;
  background-color: rgba(153, 153, 153, 0.1);
  line-height: 30px;
  margin: 10px 0;
}

.search-body .site-item {
  display: block;
  padding: 10px 10px;
  border-radius: 5px;
  background-color: #fafafa;
  margin-bottom: 10px;
  position: relative;
}
.search-body .site-item .item-name {
  height: 30px;
  font-size: 12px;
  font-weight: 700;
  color: #30333C;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  transition: all 0.3s;
}
.search-body .site-item img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
  vertical-align: middle;
}
.search-body .site-item .item-desc {
  color: #A1A7B7;
  height: 42px;
  overflow: hidden;
}
.search-body .site-item .go-btn {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 30px;
  opacity: 0;
}
.search-body .site-item:hover {
  background: #6b7184;
}
.search-body .site-item:hover .item-name,
.search-body .site-item:hover .go-btn,
.search-body .site-item:hover a {
  color: #fff;
}
.search-body .site-item:hover .item-desc {
  color: #c0c8dc;
}
.search-body .site-item:hover .go-btn {
  bottom: 20px;
  font-size: 40px;
  opacity: 1;
}

.app-item,
.game-item,
.theme-item {
  display: flex;
  margin-bottom: 0.8rem;
}
.app-item .thumb,
.game-item .thumb,
.theme-item .thumb {
  width: 5rem;
}
.app-item .thumb img,
.game-item .thumb img,
.theme-item .thumb img {
  width: 5rem;
  height: 5rem;
}
.app-item .info,
.game-item .info,
.theme-item .info {
  flex: 1;
  padding-left: 0.5rem;
}
.app-item .info .title,
.game-item .info .title,
.theme-item .info .title {
  line-height: 2rem;
  font-weight: 600;
  color: #333;
}
.app-item .info .desc,
.game-item .info .desc,
.theme-item .info .desc {
  color: #666;
  min-height: 2rem;
}
.app-item .info .tags,
.game-item .info .tags,
.theme-item .info .tags {
  font-size: 0.8rem;
  color: #777;
}

.article-item {
  display: flex;
  margin-bottom: 0.8rem;
}
.article-item .thumb {
  width: 5rem;
}
.article-item .thumb img {
  width: 5rem;
  height: 5rem;
}
.article-item .info {
  flex: 1;
  padding-left: 0.5rem;
}
.article-item .info .title {
  line-height: 2rem;
  font-weight: 600;
  color: #333;
}
.article-item .info .desc {
  color: #666;
  min-height: 2rem;
}
.article-item .info .tags {
  font-size: 0.8rem;
  color: #777;
}
.article-item:hover {
  border: 1px dotted #ccc;
  border-radius: 0.2rem;
}
.article-item:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}

.search-input-bar {
  display: flex;
  margin-bottom: 20px;
}
.search-input-bar input {
  flex: 1;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem 0 0 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.search-input-bar input:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.search-input-bar button {
  color: #fff;
  background-color: #0062cc;
  border-color: #005cbf;
  border-radius: 0 0.25rem 0.25rem 0;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  user-select: none;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
}

.self-box {
  background-color: #fff;
  margin-bottom: 10px;
}
.self-box .custom-btn,
.self-box .add-btn {
  float: right;
}
.self-box .add-btn {
  display: none;
}
.self-box .group-item {
  display: grid;
  grid-template-columns: 70px 1fr;
  grid-gap: 10px;
}
.self-box .group-item .group-name {
  line-height: 50px;
  text-align: right;
  font-weight: 700;
}
.self-box .site-item {
  display: inline-block;
  text-align: center;
  line-height: 45px;
  border-radius: 3px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: all 0.3s ease;
  width: 120px;
}
.self-box .site-item .fa {
  display: none;
}
.self-box .site-item:hover {
  color: #FFF;
  font-size: 16px;
  font-weight: bold;
  background: #6b7184;
}
.self-box .panel-footer {
  background: #6b7184;
  height: 0;
  line-height: 35px;
  text-align: center;
  overflow: hidden;
  transition: all 0.2s;
}
.self-box .panel-footer .btn {
  display: inline-block;
  margin: 30px 15px;
  padding: 0 20px;
  color: rgba(255, 255, 255, 0.6);
  border: 2px solid rgba(255, 255, 255, 0.6);
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
}
.self-box .panel-footer .btn:hover {
  color: #6b7184;
  border-color: #fff;
  background: #fff;
}
.self-box .panel-footer .btn.panel-close {
  color: #6b7184;
  border-color: #fff;
  background: #fff;
}
.self-box.edit-mode .add-btn {
  display: block;
}
.self-box.edit-mode .panel-footer {
  height: 100px;
}
.self-box.edit-mode .site-item .fa {
  display: inline-block;
}

