  font-weight: 700;
}
.comment-item .comment-author .date {
  float: left;
}
.comment-item .comment-author::after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
.comment-item .comment-footer {
  position: absolute;
  top: 1.5em;
  right: 4.6875375%;
}
.comment-item .children {
  padding-left: 1.5em;
  border-left: solid 1px #f4f4f4;
  margin: 0.75em 0;
}

@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 768px) {
  header .nav-bar > ul {
    display: none;
  }
  header .nav-bar .nav-toggle {
    display: inline-block;
  }
  header .nav-bar li a {
    display: block;
  }
  header .nav-bar.open {
    float: none;
  }
  header .nav-bar.open .nav-toggle {
    position: absolute;
    right: 10px;
    top: 0;
  }
  header .nav-bar.open .nav-toggle::before {
    content: "\f00d";
  }
  header .nav-bar.open > ul {
    display: block;
  }
  header .nav-bar.open > ul > li {
    display: block;
    border-bottom: 1px #ccc dashed;
  }
  header .nav-bar.open > ul > li > a {
    margin: 0;
  }
  header .nav-bar.open > ul > li .sub-menu {
    box-shadow: inset 0px 5px 5px -5px #000, inset 0px -5px 5px -5px #000;
    display: block;
    position: static;
  }

  .search-box {
    width: 100%;
  }

  .site-bg {
    padding-top: 10px;
  }

  #content-list .article-item {
    grid-template-columns: 103px 1fr;
    height: 90px;
  }
  #content-list .article-item .thumb {
    width: 99px;
    height: 66px;
  }
  #content-list .article-item .info .title {
    max-height: 44px;
    overflow: hidden;
    font-size: 16px;
  }
  #content-list .article-item .info .desc {
    display: none;
  }
}